import styled from "styled-components";

export const SearchButton = styled.button`
  background: rgb(95, 202, 198);
  font-size: 16px;
  color: white;
  border: 0.5px solid #c9d0e1;
  border-radius: 4px;
  width: 100px;
  height: 40px;
  outline: 0;
  cursor: pointer;
`;



